@font-face {
  font-family: "Montserrat";
  src: url("../fonts/../fonts/Montserrat-Bold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Montserrat";
  src: url("../fonts/../fonts/Montserrat-Bolditalic.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: italic;
}

@font-face {
  font-family: "Montserrat";
  src: url("../fonts/../fonts/Montserrat-SemiBold.woff2") format("woff2");
  font-weight: 600;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Montserrat";
  src: url("../fonts/../fonts/Montserrat-Medium.woff2") format("woff2");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Montserrat";
  src: url("../fonts/../fonts/Montserrat-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

/* Reset styles */

* {
  padding: 0;
  margin: 0;
  border: 0;
  outline: 0;
  vertical-align: baseline;
  background: transparent;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a {
  color: inherit;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

nav ul {
  list-style: none;
}

img {
  vertical-align: top;
}

nav,
footer,
header,
aside,
label {
  display: block;
}

input,
button,
textarea {
  font-family: inherit;
  color: inherit;
}

::placeholder {
  color: inherit;
  font-family: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: 400;
}

/* swiper-slider */

.swiper {
  margin-left: auto;
  margin-right: auto;
  position: relative;
  overflow: hidden;
  list-style: none;
  padding: 0;
  /* Fix of Webkit flickering */
  z-index: 1;
}

.swiper-vertical > .swiper-wrapper {
  flex-direction: column;
}

.swiper-wrapper {
  position: relative;
  width: 100%;
  height: 100%;
  z-index: 1;
  display: flex;
  transition-property: transform;
  box-sizing: content-box;
}

.swiper-android .swiper-slide,
.swiper-wrapper {
  transform: translate3d(0px, 0, 0);
}

.swiper-pointer-events {
  touch-action: pan-y;
}

.swiper-pointer-events.swiper-vertical {
  touch-action: pan-x;
}

.swiper-slide {
  flex-shrink: 0;
  width: 100%;
  height: 100%;
  position: relative;
  transition-property: transform;
}

.swiper-slide-invisible-blank {
  visibility: hidden;
}

/* Auto Height */

.swiper-autoheight,
.swiper-autoheight .swiper-slide {
  height: auto;
}

.swiper-autoheight .swiper-wrapper {
  align-items: flex-start;
  transition-property: transform, height;
}

.swiper-backface-hidden .swiper-slide {
  transform: translateZ(0);
  backface-visibility: hidden;
}

/* 3D Effects */

.swiper-3d,
.swiper-3d.swiper-css-mode .swiper-wrapper {
  perspective: 1200px;
}

.swiper-3d .swiper-wrapper,
.swiper-3d .swiper-slide,
.swiper-3d .swiper-slide-shadow,
.swiper-3d .swiper-slide-shadow-left,
.swiper-3d .swiper-slide-shadow-right,
.swiper-3d .swiper-slide-shadow-top,
.swiper-3d .swiper-slide-shadow-bottom,
.swiper-3d .swiper-cube-shadow {
  transform-style: preserve-3d;
}

.swiper-3d .swiper-slide-shadow,
.swiper-3d .swiper-slide-shadow-left,
.swiper-3d .swiper-slide-shadow-right,
.swiper-3d .swiper-slide-shadow-top,
.swiper-3d .swiper-slide-shadow-bottom {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: 10;
}

.swiper-3d .swiper-slide-shadow {
  background: rgba(0, 0, 0, 0.15);
}

.swiper-3d .swiper-slide-shadow-left {
  background-image: linear-gradient(to left, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}

.swiper-3d .swiper-slide-shadow-right {
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}

.swiper-3d .swiper-slide-shadow-top {
  background-image: linear-gradient(to top, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}

.swiper-3d .swiper-slide-shadow-bottom {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}

/* CSS Mode */

.swiper-css-mode > .swiper-wrapper {
  overflow: auto;
  scrollbar-width: none;
  /* For Firefox */
  -ms-overflow-style: none;
  /* For Internet Explorer and Edge */
}

.swiper-css-mode > .swiper-wrapper::-webkit-scrollbar {
  display: none;
}

.swiper-css-mode > .swiper-wrapper > .swiper-slide {
  scroll-snap-align: start start;
}

.swiper-horizontal.swiper-css-mode > .swiper-wrapper {
  scroll-snap-type: x mandatory;
}

.swiper-vertical.swiper-css-mode > .swiper-wrapper {
  scroll-snap-type: y mandatory;
}

.swiper-centered > .swiper-wrapper::before {
  content: "";
  flex-shrink: 0;
  order: 9999;
}

.swiper-centered.swiper-horizontal > .swiper-wrapper > .swiper-slide:first-child {
  margin-inline-start: var(--swiper-centered-offset-before);
}

.swiper-centered.swiper-horizontal > .swiper-wrapper::before {
  height: 100%;
  width: var(--swiper-centered-offset-after);
}

.swiper-centered.swiper-vertical > .swiper-wrapper > .swiper-slide:first-child {
  margin-block-start: var(--swiper-centered-offset-before);
}

.swiper-centered.swiper-vertical > .swiper-wrapper::before {
  width: 100%;
  height: var(--swiper-centered-offset-after);
}

.swiper-centered > .swiper-wrapper > .swiper-slide {
  scroll-snap-align: center center;
}

/* global styles*/

body {
  overflow-x: hidden;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.3;
  letter-spacing: -0.28px;
  color: rgb(17, 17, 17);
  background-color: rgb(250, 250, 250);
}

:root {
  scroll-behavior: smooth;
}

.none {
  display: none;
}

.visible {
  display: block;
}

.no-scroll {
  overflow: hidden;
}

.hidden {
  width: 0;
  height: 0;
  opacity: 0;
  visibility: hidden;
  user-select: none;
  pointer-events: none;
}

.container {
  margin: 0 auto;
  max-width: 1166px;
  padding: 0 15px;
}

.main-title {
  text-align: center;
  font-size: 28px;
  font-weight: 500;
  line-height: 1.21;
  letter-spacing: -0.56px;
  color: rgb(255, 255, 255);
}

.main-title span {
  color: rgb(253, 146, 34);
}

.section-title {
  max-width: 411px;
  margin: 0 auto;
  text-align: center;
  font-size: 24px;
  font-weight: 600;
  line-height: 1.16;
  letter-spacing: -0.72px;
  text-transform: uppercase;
}

.section-title span {
  color: rgb(253, 146, 34);
}

.button {
  display: block;
  padding: 14px 40px;
  border-radius: 100px;
  font-weight: 600;
  line-height: normal;
  transition: all 0.3s;
}

.main-button {
  color: rgb(255, 255, 255);
  background-color: rgb(253, 146, 34);
}

.main-button:hover {
  background-color: rgb(252, 241, 224);
  color: rgb(253, 146, 34);
  transition: all 0.3s;
}

.hero-button {
  border: 1px solid rgb(255, 255, 255);
  color: rgb(255, 255, 255);
}

.hero-button:hover {
  border: 1px solid rgb(253, 146, 34);
  color: rgb(253, 146, 34);
  transition: all 0.3s;
}

.review-button {
  border: 1px solid rgb(253, 146, 34);
  font-size: 18px;
  color: rgb(17, 17, 17);
}

.review-button:hover {
  border: 1px solid rgb(253, 146, 34);
  color: rgb(255, 255, 255);
  background-color: rgb(253, 146, 34);
  transition: all 0.3s;
}

/* Header styles */

.header {
  width: 100%;
  padding: 24px 0 32px 0;
  border: 1px solid red;
}

.header__inner {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.logo {
  width: 100%;
  max-width: 211px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.logo .svg {
  width: 24px;
  height: 24px;
  stroke: rgb(253, 146, 34);
  stroke-width: 2;
  fill: none;
  transition: all 0.3s;
}

.logo .svg:hover {
  stroke-dashoffset: 0;
  stroke-dasharray: 80;
}

.logo__letter {
  font-size: 18px;
  font-style: italic;
  font-weight: 700;
  line-height: 1.1;
  letter-spacing: -3.7px;
  text-transform: uppercase;
}

.logo__letter:nth-child(n+8) {
  font-style: normal;
  letter-spacing: -3.5px;
}

.nav {
  width: 100%;
  max-width: 532px;
  margin-right: -54px;
}

.nav__list {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.nav__link {
  font-weight: 500;
  line-height: 1.14;
  transition: color 0.3s;
}

.nav__link:hover {
  color: rgb(253, 146, 34);
}

.nav__social {
  display: none;
}

.nav__social-link {
  width: 21px;
  height: 21px;
}

.social {
  width: 66px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.social svg {
  width: 24px;
  height: 24px;
  stroke: rgb(17, 17, 17);
  stroke-width: 2;
  fill: none;
  transition: all 0.3s;
}

.social__link {
  width: 24px;
  height: 24px;
}

.social__link:nth-child(1) {
  margin-right: 18px;
}

.social__link:hover svg {
  stroke: rgb(253, 146, 34);
}

.burger {
  display: none;
  width: 24px;
  height: 16px;
  position: relative;
  transition: all 0.3s;
  z-index: 10;
}

.burger:hover .burger__span {
  background-color: rgb(253, 146, 34);
}

.burger__span {
  position: absolute;
  left: 0;
  width: 100%;
  height: 2.2px;
  border-radius: 1px;
  background-color: rgb(17, 17, 17);
  transition: all 0.3s;
}

.burger__span-one {
  top: 0;
}

.burger__span-two {
  top: 50%;
}

.burger__span-three {
  top: 100%;
}

/* modals styles*/

footer {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 100px;
}

@media (max-width: 950px) {
  .logo {
    max-width: 169px;
  }

  .logo .svg {
    width: 21px;
    height: 21px;
    stroke-width: 1.8;
  }

  .logo__letter {
    font-size: 14px;
  }

  .nav {
    margin-right: 0;
    max-width: 483px;
  }

  .social {
    width: 56px;
  }

  .social svg {
    width: 21px;
    height: 21px;
    stroke-width: 1.8;
  }

  .social__link {
    width: 21px;
    height: 21px;
  }

  .social__link:nth-child(1) {
    margin-right: 13px;
  }
}

@media (max-width: 768px) {
  .header__nav {
    position: absolute;
    top: 0;
    right: 0;
    transform: translateX(100%);
    transition: all 0.8s;
    max-width: 290px;
    height: 100vh;
    padding: 156px 0 0 85px;
    background-color: rgb(17, 17, 17);
  }

  .header__nav .nav__list {
    flex-direction: column;
    align-items: start;
  }

  .header__nav .nav__item:not(:last-child) {
    margin-bottom: 24px;
  }

  .header__nav .nav__link {
    color: rgb(255, 255, 255);
    transition: color 0.3s;
  }

  .header__nav .nav__link:hover {
    color: rgb(253, 146, 34);
  }

  .header__social {
    display: none;
  }

  .nav__social {
    position: absolute;
    left: 32px;
    bottom: 24px;
    width: 56px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  .nav__social svg {
    width: 21px;
    height: 21px;
    stroke: rgb(255, 255, 255);
    stroke-width: 1.8;
    fill: none;
    transition: all 0.3s;
  }

  .nav__social-link:hover svg {
    stroke: rgb(253, 146, 34);
  }

  .burger {
    display: block;
  }

  .untranslate {
    transform: translateX(0);
  }

  .span-one-transform {
    top: 50%;
    transform: rotate(45deg);
    transition: all 0.3s;
    background-color: rgb(255, 255, 255);
  }

  .span-two-transform {
    transform: rotate(-45deg);
    transition: all 0.3s;
    background-color: rgb(255, 255, 255);
  }

  .span-three-transform {
    display: none;
  }
}

@media (max-width: 375px) {
  .button {
    padding: 12px 28px;
  }

  .review-button {
    font-size: 14px;
  }

  .header__nav {
    max-width: 220px;
    padding: 136px 0 0 50px;
  }
}